@import url(https://fonts.googleapis.com/css?family=Special+Elite|Viga);
@import url(https://fonts.googleapis.com/css?family=Special+Elite|Viga);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("/pruebaPort2/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
  font-family: 'Josefin Slab', serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.containerPrincipal {
  margin: 2% 2%;
  border: 2px solid #fcfcfc;
  height: 90vh;
}
.container{
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 9vmin;
}

  .text{
    text-align: center;
    display: inline-block;     
    font-family: 'Special Elite', cursive;
    color: #fcfcfc;    
  }
  
  .text__first, .text__second{
    position: relative;
  }
  
  .text__word1{
    opacity: 0;
    line-height: 1.205;
    font-size: 8vmin;
  }
  .text__word{
    opacity: 0;
    line-height: 1.205;
    font-size: 6vmin;
  }
  .text__first-bg, .text__second-bg{
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 100;
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
  }
  
  .text__first-bg{
    background-color:  #204e4a;   
  }
  
  .text__second-bg{
    background-color: #ecbf3a;
    
  }
  
  
  .restart{
    position: absolute;
    font-size: 12px;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url("/pruebaPort2/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
    border: 2px solid #fcfcfc;
    margin: 0 auto 2%;
    color: white;
  }

.logoHome {
    position: absolute;
    top: 0;
    left: 0;
    color: white;
    background: none;
    z-index: 3;
}

.logo {
    background-image: url("/pruebaPort2/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
    width: 50%;
    padding: 1%;
}

.about, .proyect, .contact {
    position: absolute;
    right: 0;
    z-index: 3;
    color: white;
    padding: 1%;
    /* background-image: url('./../../dark-green-glow-29241-1920x1080\(1\).jpg'); */
    text-shadow: 5px 5px 5px #000;
    text-decoration: none;

}
.about {
    margin-right: 27%;
}

.proyect {
    margin-right: 15.5%;
}
.contact {
    margin-right: 5%;
}

.about:hover, .proyect:hover, .contact:hover{
    font-weight: bolder;
    border-bottom: 2px solid white;
}
.SplitPane {
    height: 100%;
    width: 100%; 
    margin: 0;
    padding: 0;
    position: relative;
    -webkit-animation: fadeIn 500ms ease-in-out forwards;
            animation: fadeIn 500ms ease-in-out forwards;
    background-size: cover;
    -webkit-transition: background-image 500ms ease-in-out;
    transition: background-image 500ms ease-in-out;
  }
  
  
  .SplitPane-left {
    float: left;
    width: 30%;
    text-align: right;
  }
  
  .SplitPane-right {
    float: left;
    width: 70%;
    color: #ffccff;
    text-align: left;
  }

.foto{
  margin-top:15%;
  border-radius: 10px;
  box-shadow: 0 0 15px #001c1b;
}  

.biografy{
    margin-left: 10%;
    margin-right: 10%;
    margin-top: 6%;
    display: block;
}
.titulo,.skills{
    color: #fcfcfc;
    font-weight: bolder;
    font-family: 'Special Elite', cursive;
    margin-bottom: auto
       
}
.titulo{
  font-size: 10vmin;
}
.skills{
  text-align: center;
}

.parrafo{
    text-indent: 2em; 
    margin-left: 5%;
    margin-right: 5%;
    margin-bottom: 5%;
    text-align: justify;
    font-weight: 500
  }

.boton{
    border: 1px solid #ffccff;
    color: #fcfcfc;
    background: none;
    position: absolute;
    padding: 1%; 
    text-decoration: none;
    text-align: center;
}
.boton:hover {
    background-color: #fcfcfc;
    border: 1px solid #001c1b;
    color: #001c1b;
  }
.icono{
  font-size: 7vmin;
  margin:auto 2%;
}
.iconoNodeGit{
  font-size: 10vmin;
  margin: auto 2%;
}
.icons{
  margin-bottom: 2%;
  text-align: center;
}

.first-bg{
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  right:  0;
  top: 0;  
  z-index: 100; 
  -webkit-transform-origin: left; 
          transform-origin: left;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  background-color:  #204e4a;
}





