.wrapper {
	width: 100%;
	height: 100%;
	margin: 0;
	padding: 0;
	position: relative;
	-webkit-animation: fadeIn 500ms ease-in-out forwards;
	        animation: fadeIn 500ms ease-in-out forwards;
	background-size: cover;
	-webkit-transition: background-image 500ms ease-in-out;
	transition: background-image 500ms ease-in-out;
}

.selectors {
	position: absolute;
	bottom: 0;
	top: 0;
	right: 0;
	width: 40px;
	/* background: rgba(0, 0, 0, 0.2); */
	/* height: 100%; */
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	z-index: 1;
	
}

.selector {
	border: 1px solid #fff;
	width: 20px;
	height: 20px;
	border-radius: 50%;
	margin: 5px auto;
	-webkit-transition: background .3s ease-in-out, opacity .3s ease-in-out;
	transition: background .3s ease-in-out, opacity .3s ease-in-out;
	background: rgba(0, 0, 0, 0.2);
	
}
.selector:hover,
.selector.active {
		background: #fff;		
		opacity: 1;
}

.panel-left {
  float: left;
	width: 40%;
	height: 100%;
	text-align: right;	
	box-shadow: 5px 0 25px 0 rgba(0, 0, 0, 0.3);
	-webkit-transition: background 500ms ease-in-out;
	transition: background 500ms ease-in-out;
	background-image: url("/pruebaPort2/static/media/dark-green-glow-29241-1920x1080(1).18d40436.jpg");
  }
  
.panel-right {
  float: left;
	width: 60%;
	height: 100%;
  color: #ffccff;
  text-align: left;
  }

.panel {
	width: 100%;
	height: 100%;	
}

.imagenPanel{
	width: 80%;
	height: 70%;
	margin: 10%;
	border-radius: 10px;
	box-shadow: 1px 1px 15px #001c1b;
}

.panelfirst-bg{
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  right:  0;
  bottom: 0;  
  z-index: 1000;
  -webkit-transform-origin: right;
          transform-origin: right;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  background-color:  #204e4a;
}
h2.panel-header,
	p.panel-info {
		margin: 0;
		padding: 1.3rem 3.5rem;
	}
	h2.panel-header {
		padding-top: 2em;
		font-weight: normal;
		font-size: 2.5rem;
		text-transform: capitalize;
		color: #fff;
		font-family: 'Special Elite', cursive;
		font-weight: bolder;
		text-align: center;
	}

	p.panel-info {
		font-size: 1.2rem;
		line-height: 1.8em;
		color: #fff;
	}

	button.panel-button {
		margin: 1.3rem 3.5rem;
		padding: 0.8em 1.3em;
		background: none;
		border: 1px solid;
		color: #fff; 
		cursor: pointer;
		font-size: 0.8rem;
		font-weight: 700;
		text-transform: uppercase;
		-webkit-transition: box-shadow 0.5s ease-in-out, color 0.5s ease-in-out;
		transition: box-shadow 0.5s ease-in-out, color 0.5s ease-in-out;
		
	}
	button.panel-button:hover {
		box-shadow: inset 0 0 1.5em 1.5em #fff;
		color: #001c1b
	}
	button.panel-button:focus {
		outline: none;
	}
  

@-webkit-keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
  

@keyframes fadeIn {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.contactarse{
    color: #fcfcfc;
